/* eslint-disable prettier/prettier */
<template>
  <div class="nav-elements">
    <div class="menu">
      <div class="logo">
        <img src="../assets/samasal.png" />
        <h4>Salamiyah News</h4>
      </div>
      <div class="elements">
        <router-link  to="/">News</router-link> |
        <router-link to="/sport">Sport</router-link> |
        <router-link to="/economy">Economy</router-link> |
        <router-link to="/health">Health</router-link> |
        <router-link to="/about">About Us</router-link>
      </div>
<!--Mobile Drop Down List-->
      <div id="mob-elements-menu" class="mob-elements-menu">

        <div id="mob-drop-list-btn drop-btn" class="mob-drop-list-btn">
          <button @click="dropDownList" class="drop-down-list fa fa-bars" ></button>
        </div>

        <div  id="mob-drop-list" class="mob-drop-list">
          <router-link  to="/"><p @click="closeDropDown"> News</p></router-link> <p class="borderr"></p>
          <router-link to="/sport"><p @click="closeDropDown">Sport </p></router-link><p class="borderr"></p> 
          <router-link to="/economy"><p @click="closeDropDown">Economy </p></router-link> <p class="borderr"></p>
          <router-link to="/health"><p @click="closeDropDown">Health </p></router-link><p class="borderr"></p>
          <router-link to="/about"><p @click="closeDropDown">About Us </p></router-link>          
        </div>

      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: "NavEle",

  methods : {

     dropDownList: function() {
     document.getElementById("mob-drop-list").style.display = "block";
     document.getElementById("mob-elements-menu").style.right = "100%";
    document.getElementById("drop-btn").style.left = "5px";
    },

    closeDropDown: function() {
      document.getElementById("mob-drop-list").style.display = "none";
      document.getElementById("mob-elements-menu").style.right = "10%";

    },
  
  },
};
</script>

<style lang="scss">
@import url("https://fonts.googleapis.com/css2?family=Indie+Flower&family=Lobster&family=Source+Sans+Pro&display=swap");
$orangeCol: #fec194;
$redCol: #ff0061;
$darkblueCol: #2b3f53;
.elements {
  background-color: $orangeCol;
  font-family: "Lobster", cursive;
  text-align: right;
  right: 40px;
  
}
.mob-elements-menu{
  display: none;
  background-color: $orangeCol;
  position: fixed;
  opacity: 1;
  .mob-drop-list{
    padding-top: 120px;
    position: fixed;
    
    a{
      display: block;
       margin-bottom: 25px;
       margin-top: 25px;
    }
    .borderr{
      border-bottom: 2px solid $darkblueCol;
    }

  }
}
.logo {
  width: 550px;
  height: 75px;
  float: left;
  font-weight: bold;
  color: $darkblueCol;

  img {
    width: 140px;
    height: 80px;
    mix-blend-mode: multiply;
    float: left;
  }
  h4 {
    font-family: "Indie Flower", cursive;
    font-weight: bolder;
    padding-top: 40px;
    right: 10%;
    width: 350px;
  }
}

@media only screen and (max-width: 600px) {
  .nav-elements{
    width: 100%;
    .menu{
      width: 100%;
      .logo{
        width: 98%;

        img{
         width: 33%;
         margin-left:2%;
        }
        h4{
          width: 50%;
         
        }
      } 
      .elements {
        right: 0px;
        display: none;
      }
      .mob-elements-menu{
        display: block;
        position: absolute;
        right: 8%;
        z-index: 3;
      }

        .mob-drop-list-btn{
          position: absolute;
          top: 50px;
          z-index: 2;
