            transition: 0.4s;
            font-size: 16px;
            background-color: $darkblueCol;
            border: 2px solid $redCol;
            box-shadow: 3px 3px 3px $darkblueCol;
            color: $orangeCol;
        }
        }
        .mob-drop-list{
          display: none;
          
          width: 100%;
          position: fixed;
          height: 850px;
          background-color: $orangeCol;
          z-index: 1;
        }
      
    }
  }


}

@media only screen and (max-width : 300px) {
  .nav-elements .menu .logo h4{
    font-size: 16px;
  }
}
</style>
